import React from 'react';
import { List, ListItem, Avatar, ListItemText, Divider, Box, Button } from '@mui/material';

class ChatComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      translations: {}, // Store translations for each message
    };

    this.translateMessage = this.translateMessage.bind(this);
  }

  translateMessage(message) {
    // Replace 'YOUR_GOOGLE_TRANSLATE_API_KEY' with your actual API key
    const apiKey = 'AIzaSyBRmzlBUfuw9uA_aUcb2R0nxGa2HHp83Zk';
    const { content, language } = message;

    // Make an API call to Google Translate
    fetch(
      `https://translation.googleapis.com/language/translate/v2?key=${apiKey}&q=${content}&source=${language}&target=en`
    )
      .then((response) => response.json())
      .then((data) => {
        const translation = data.data.translations[0].translatedText;

        // Update the translations state
        this.setState((prevState) => ({
          translations: {
            ...prevState.translations,
            [message.id]: translation,
          },
        }));
      })
      .catch((error) => {
        console.error('Translation error:', error);
      });
  }

  renderMessages() {
    const { messages } = this.props;
    const { translations } = this.state;

    return messages.map((message, index) => (
      <Box margin="auto" maxWidth="400px" key={index}>
        <div>
          <ListItem
            sx={{
              display: 'flex',
              bgcolor: message.isAI ? '#EDEDED' : '#DCF8C6',
              maxWidth: '300px',
              borderRadius: '8px',
              margin: 'auto',
              marginLeft: message.isAI ? '0px' : '50px',
            }}
          >
            {/* <Avatar sx={{ width: 24, height: 24 }} alt={message.isAI ? 'AI' : 'User'} src={message.avatarUrl} /> */}
            <ListItemText
              primary={message.content}
              secondary={message.isAI ? 'AI' : 'User'}
              sx={{ textAlign: 'left' }}
            />
            {message.language !== 'en' && (
              <Button onClick={() => this.translateMessage(message)}>Translate</Button>
            )}
            {translations[message.id] && (
              <ListItemText primary={`Translation: ${translations[message.id]}`} />
            )}
          </ListItem>
          {index < messages.length - 1 && <br />}
        </div>
      </Box>
    ));
  }

  render() {
    return <List>{this.renderMessages()}</List>;
  }
}

export default ChatComponent;
